const ADD_MESSAGE = "social-network/dialogs/ADD-MESSAGE";

export const sendMessageActionCreator = (myMessage) => ({
  type: ADD_MESSAGE,
  myMessage: myMessage
})

const initialState = {
  dialogs : [
    { name: "Sasha", id: 1, ava: "data:image/jpeg;base64,"},
    { name: "Dima", id: 2, ava: "data:image/jpeg;base64,"},
    { name: "Kiborg", id: 3, ava: "data:image/jpeg;base64,"},
    { name: "Kion", id: 4, ava: "data:image/jpeg;base64,"},
    { name: "Masya", id: 5, ava: "data:image/jpeg;base64,"}
  ],
  messages: [
    {id: 1, message: "Hello, how are you?"},
    {id: 2, message: "I'm fine, thank you"},
    {id: 3, message: "Yo"},
    {id: 4, message: "Yo"},
    {id: 5, message: "Yo"}
  ]
}

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case ADD_MESSAGE: {
      return {...state,
        messages: [...state.messages, {id: state.messages.length + 1, message: action.myMessage}]
      };
    }
    default:
      return state;
  }
}

export default dialogsReducer;